<template>
  <div>
    <v-container class="ma-0 pa-0" grid-list-sm>
      <v-subheader> All Books </v-subheader>

      <v-layout wrap>
        <v-flex v-for="book in books" :key="`book-` + book.id" xs6 lg3>
          <!-- <v-card :to="'/book/' + book.slug">
            <v-img
              :src="getImage('/books/' + book.cover)"
              class="white--text my-15"
            >
              <v-card-title class="fill-height align-end" v-text="book.title">
              </v-card-title>
            </v-img>
          </v-card> -->
          <book-item :book="book" />
        </v-flex>
      </v-layout>
    </v-container>

    <template>
      <div class="text-center">
        <v-pagination
          v-model="page"
          @input="go"
          :length="lengthPage"
          :total-visible="5"
          class="my-5"
          circle
        >
        </v-pagination>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  data: () => ({
    books: [],
    page: 0,
    lengthPage: 0,
  }),

  components: {
    BookItem: () => import("@/components/BookItem.vue"),
  },

  created() {
    this.go();
  },
  methods: {
    go() {
      let url = "/books?page=" + this.page;
      this.axios
        .get(url)
        .then((response) => {
          let { data } = response.data;
          let { meta } = response.data;
          this.books = data;
          // jumlah halaman di dapat dari meta endpoint books
          this.lengthPage = meta.last_page;
          this.page = meta.current_page;
        })
        .catch((error) => {
          let { response } = error;
          console.log(response);
        });
    },
  },
};
</script>
